/* centering content using flex */
.home-wrap__content {
  width: 95%;
  margin: 0 auto;
}

.section {
  height: 600px;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 30% 70%;
      grid-template-columns: 30% 70%;
      grid-template-areas: "div1 div2";
  border-bottom: 2px solid #14f1cb;
}

.section__div1 {
  -ms-grid-row: 1;
  -ms-grid-column: 1;
  grid-area: div1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.section__div1__list__item {
  line-height: 1.5em;
  list-style: none;
  display: flex;
  justify-content: flex-start;
}

.section__div1__list__item button {
  text-align: start;
}

.section__div1__list__item button:hover {
  background-color: #14f1cb;
}

.section__div2 {
  -ms-grid-row: 1;
  -ms-grid-column: 2;
  grid-area: div2;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.section__div2__bg {
  width: 7em;
  height: 2em;
  background-color: #14f1cb;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5%;
}

.section__div2__bg__text {
  margin: 0 auto;
}
